// Variables
// ---------
:root {
	--body-font-size                            : 16px;
	--body-font                                 : "Ubuntu", system-ui, -apple-system, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
	--body-font-alt                             : Georgia, Times, Times New Roman, serif;
	--body-line-height                          : 161%;
	--body-color                                : #0B0334;
	--body-background                           : #fff;
}

// Breakpoints
$break-desktop     : 1439px;
$break-desktop-max : (1081 - 0.02) * 1px;
$break-table       : 767px;
$break-table-max   : (681 - 0.02) * 1px;
