// -----------------------------------------------
//  Form config
// -----------------------------------------------

$input-background          : #f5f4f4;
$input-border              : 1px solid  #7a7a7a;
$input-padding             : 10px 10px;
$input-radius              : 7px;

$input-font-size           : 14px;
$input-font-weight         : 400;
$input-line-height         : 1.25;
$input-color               : #000;
$input-transition          : border-color .3s ease;

// Placeholder
$input-placeholder         :#7a7a7a;;

// Focus
$input-border-focus        : #412b92;

// Error
$input-border-error        : var(--red);

// Disabled
$input-border-disabled     : #BCBCBC;
$input-background-disabled : #F9F9F9;
$input-color-disabled      : #939393;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form__input {
	font-size          : $input-font-size;
	font-weight        : $input-font-weight;
	line-height        : $input-line-height;
	box-sizing         : border-box;
	max-width          : 100%;
	height             : auto;
	padding            : $input-padding;
	transition         : $input-transition;
	color              : $input-color;
	border             : $input-border;
	border-radius      : $input-radius;
	outline            : 0;
	background-color   : $input-background;
	-webkit-appearance : none;

	&::placeholder {
		font-weight : $input-font-weight;
		color       : $input-placeholder;
	}

	&:focus {
		border-color : $input-border-focus;
	}

	&.error {
		color        : var(--red);
		border-color : $input-border-error;
	}

	&.disabled,
	&[disabled] {
		cursor           : default;
		color            : $input-color-disabled;
		border-color     : $input-border-disabled;
		background-color : $input-background-disabled;
	}

	&[readonly] {
		cursor           : default;
		color            : $input-color-disabled;
		border-color     : $input-border-disabled;
		background-color : $input-background-disabled;
	}
}


textarea {
	overflow : auto;
	height   : auto;
}

.error-message {
	order      : 1;
	margin-top : 5px;
	color      : var(--red);
}

label.checkbox-style,
.checkbox-style {
	width: 18px;
	height: 18px;
	position                    : relative;
	display                     : block;
	overflow                    : hidden;
	margin                      : 0;
	cursor                      : pointer;
	user-select                 : none;
	outline                     : 0;
	-webkit-tap-highlight-color : transparent;
	flex-shrink                 : 0;

	> span {
		position       : relative;
		display        : inline-block;
		padding        : 2px 0 2px 28px;
		vertical-align : top;
		color          : #A7A8B1;

		&:before {
			content       : "";
			position      : absolute;
			z-index       : 1;
			top           : 0;
			left          : 0;
			box-sizing    : border-box;
			width         : 18px;
			height        : 18px;
			transition    : border-color .3s ease, background-color .3s ease;
			border        : 1px solid #412B92;
			border-radius: 3px;
		}

		&:after {
			content           : "";
			position          : absolute;
			z-index           : 5;
			top: 3px;
			left: 3px;
			width             : 12px;
			height            : 13px;
			transition        : opacity .3s ease;
			opacity           : 0;
			background-image  : url("../images/icon/check.svg");
			background-repeat : no-repeat;
			background-size   : cover;

		}
	}

	input[type="checkbox"] {
		position                    : absolute;
		top                         : 0;
		left                        : -30px;
		width                       : 1px;
		height                      : 1px;
		outline                     : 0;
		-webkit-tap-highlight-color : transparent;

		&:checked {
			~ span {
				&:before {
					background: #412B92;
				}

				&:after {
					opacity : 1;
				}
			}
		}

		&.disabled,
		&[disabled] {
			~ span {
				&:after {
					background : #E3E3E3;
				}
			}
		}
	}
}


.checkbox-wrap {
	display     : flex;
	align-items : center;
	gap         : 12px;

	@media screen and (min-width : $break-table) {
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.checkbox-wrap__text {
	font-weight: 400;
	font-size: 10px;
	line-height: 100%;
	color: #000;

	@media screen and (min-width : $break-table) {

	}

