.page-wrapper {
	overflow : hidden;
}

.container {
	box-sizing : border-box;
	width      : (766 + 50) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 25px;

	@media screen and (min-width : $break-table) {
		width      : (766 + 84) * 1px;
		padding    : 0 42px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.list-reset {
	padding: 0;
	list-style: none;
	margin: 0;
}

.scroll {
	overflow: hidden;
	position: fixed;
	width: 100%;
}
.section-title {
	font-weight: 700;
	font-size: 20px;
	line-height: 120%;
	text-align: center;
	color: #000;

	@media screen and (min-width : $break-table) {
		font-size: 28px;
		line-height: 100%;
	}

	@media screen and (min-width : $break-desktop) {

	}

	span {
		color: #412b92;
	}
}

.section--offset {
	padding: 25px 0;
}

.hero__img {
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.hero {
	position: relative;
	height: 250px;
}
.hero__main {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.hero__container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	height: 100%;
}

.hero__title {
	font-weight: 700;
	font-size: 26px;
	line-height: 100%;
	color: #000;
	max-width: 299px;
	margin-bottom: 56px;
	@media screen and (min-width : $break-table) {
		font-size: 36px;
		line-height: 100%;
		max-width: 414px;
		margin-bottom: 42px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__container {
	width      : (766 + 50) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 25px;

	@media screen and (min-width : $break-table) {
		width      : (766 + 84) * 1px;
		padding    : 0 42px;
	}
}

.swiper--hero {
	height: 250px;
	.swiper-slide {
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}


.clients {
	padding: 50px 0 25px 0;
}

.clients__title {
	margin-bottom: 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom: 30px;
	}
}

.clients__grid {
	display: grid;
	grid-template-columns: 1fr;
	gap: 10px;
	@media screen and (min-width : $break-table) {
		grid-template-columns: 1fr 1fr;
		grid-column-gap: 70px;
		grid-row-gap: 10px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.clients-item {
	display: flex;
	align-items: center;
	gap: 10px;

	@media screen and (min-width : $break-table) {
		gap: 15px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.clients-item__icon {
	flex-shrink: 0;
	width: 30px;
	height: 30px;

	@media screen and (min-width : $break-table) {
		width: 35px;
		height: 35px;

	}

	@media screen and (min-width : $break-desktop) {

	}
}


.clients-item__name {
	font-weight: 400;
	font-size: 16px;
	line-height: 100%;
	color: #000;

	@media screen and (min-width : $break-table) {
		font-size: 20px;
		line-height: 100%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.help__title {
	margin-bottom: 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom: 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.help__wrapper {
	display: flex;
	grid-gap: 20px;
	@media screen and (min-width : $break-table) {
		grid-gap: 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
.help__buttons {
	width: 50%;
}
.help__btn {
	background-color: #f5f4f4;
	border: none;
	border-radius: 7px;
	padding: 10px 15px;
	font-weight: 500;
	font-size: 16px;
	line-height: 100%;
	color: #000;
	transition: color .3s ease;
	width: 100%;
	text-align: left;
	cursor: pointer;
	@media screen and (min-width : $break-table) {
		padding: 15px 15px;
		font-size: 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:hover {
		color: #412b92;
	}

	&.active {
		color: #412b92;
	}

	&:not(:last-child) {
		margin-bottom: 10px;

		@media screen and (min-width : $break-table) {
			margin-bottom: 22px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.help__swiper {
	width: 50%;
}


.swiper--help {
	height: 100%;
	.swiper-slide {
		img {
			border-radius: 7px;
			height: 100%;
			object-fit: cover;
		}
	}
}
.advantages {
	.b-btn--primary {
		position: relative;
		left: 50%;
		transform: translateX(-50%);
	}
}
.advantages__title {
	margin-bottom: 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom: 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__grid {
	display: grid;
	grid-template-columns: repeat(4,1fr);
	grid-gap: 30px 40px;
	margin-bottom: 20px;
	@media screen and (min-width : $break-table) {
		grid-template-columns: repeat(6,1fr);
		grid-gap: 30px;
		margin-bottom: 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__item {
	grid-column: 2 span;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	@media screen and (min-width : $break-table) {
		grid-column: 3 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__icon {
	margin-bottom: 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__text {
	font-weight: 400;
	font-size: 16px;
	line-height: 120%;
	text-align: center;
	color: #000;
	max-width: 266px;

	@media screen and (min-width : $break-table) {
		font-size: 20px;
		line-height: 100%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.products {
	.b-btn--primary {
		position: relative;
		left: 50%;
		transform: translateX(-50%);
	}
}

.products__title {
	margin-bottom: 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom: 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product-item {
	border-radius: 7px;
	background-color: #f5f4f4;
	overflow: hidden;
	margin: 0 10px;
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 100%;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin: 0;

	}
}

.product-item__img {
	height: 230px;
	border-radius: 7px;
	overflow: hidden;
	transition: opacity .3s ease;
	position: relative;
	flex-shrink: 0;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	@media screen and (min-width : $break-table) {

	}


	&:hover {

		@media screen and (min-width : $break-desktop) {
			.hover{
				opacity: 1;
			}

			.default {
				opacity: 0;
			}
		}
	}

	picture {
		position: absolute;
		width: 100%;
		height: 100%;
		transition: opacity 0.3s ease;

	}

	.hover {
		opacity: 0;
		transition: opacity .3s ease;
	}
}

.product-item__name {
	font-weight: 500;
	font-size: 16px;
	line-height: 100%;
	text-align: center;
	color: #000;
	padding:20px 9px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
	@media screen and (min-width : $break-table) {
		font-size: 20px;
		line-height: 100%;

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--product {
	overflow: visible;
	margin-bottom: 30px;
	.swiper-wrapper {

		@media screen and (min-width : $break-desktop) {
			box-sizing: unset ;
			display: grid !important;
			grid-gap: 30px ;
			grid-template-columns: repeat(6, 1fr);
			transform: translate3d(0, 0, 0) !important;
		}
	}

	.swiper-slide {
		width: 70%;
		display: flex;
		height: auto;
		@media screen and (min-width : $break-table) {
			width: 34%;
		}

		@media screen and (min-width : $break-desktop) {
			width: 100%;
		}

		&:nth-child(1) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 1/5;
			}
		}

		&:nth-child(2) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 5/7;
			}
		}

		&:nth-child(3) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 1/3;
			}
		}

		&:nth-child(4) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 3/5;
			}
		}
		&:nth-child(5) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 5/7;
			}
		}

		&:nth-child(6) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 1/3;
			}
		}

		&:nth-child(7) {
			@media screen and (min-width : $break-desktop) {
				grid-column: 3/7;
			}
		}
	}
}

.feedback__wrapper {
	background-color:  #f5f4f4;
	display: grid;
	grid-template-columns: repeat(4,1fr);
	border-radius: 7px;
	@media screen and (min-width : $break-table) {
		grid-template-columns: repeat(6,1fr);
		gap: 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feedback__left {
	grid-column: 4 span;
	padding: 20px 10px 20px 10px;

	@media screen and (min-width : $break-table) {
		padding: 30px 0 30px 15px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feedback__img {
	grid-column: 4 span;

	@media screen and (min-width : $break-table) {
		grid-column: 2 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.feedback__title {
	text-align: center;
	margin-bottom: 15px;
	@media screen and (min-width : $break-table) {
		text-align: right;
		margin-bottom: 25px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feedback__text {
	font-weight: 400;
	font-size: 16px;
	line-height: 120%;
	text-align: center;
	color: #000;
	margin-bottom: 10px;

	@media screen and (min-width : $break-table) {
		font-size: 20px;
		line-height: 100%;
		margin-bottom: 15px;
		text-align: left;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
.feedback__wrap {
	max-width: 246px;
	margin: 0 auto;

	@media screen and (min-width : $break-table) {
		margin: 0;
		max-width: unset;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
.form--feedback {
	margin-bottom: 25px;

	@media screen and (min-width : $break-table) {
		max-width: 315px;
	}

	@media screen and (min-width : $break-desktop) {

	}
	.form__grid {
		display: grid;
		grid-template-columns: repeat(2,1fr);
		grid-gap: 15px;
	}

	.form__row {
		&:last-child {
			grid-column: 2 span;
		}
	}

	.b-btn--primary {
		margin-top: 15px;
		padding: 11px 15px;
		position: relative;
		left: 50%;
		transform: translateX(-50%);
		@media screen and (min-width : $break-table) {
			left: unset;
			transform: unset;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.socials {
	display: grid;
	grid-template-columns: 1fr;
	gap: 15px;
	@media screen and (min-width : $break-table) {
		grid-template-columns: 1fr 1fr;
		grid-gap: 15px 50px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.socials__item {

}

.socials__link {
	display: flex;
	align-items: center;
	gap: 10px;
	width: auto;
	font-weight: 400;
	font-size: 16px;
	line-height: 100%;
	color: #000;
	@media screen and (min-width : $break-table) {
		font-size: 20px;
		line-height: 100%;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		width: 30px;
		height: 30px;

		@media screen and (min-width : $break-table) {
			width: 35px;
			height: 35px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
