@keyframes mmslideIn {
	from {
		opacity : 0;
	}
	to {
		opacity : 1;
	}
}

@keyframes mmslideOut {
	from {
		opacity : 1;
	}
	to {
		opacity : 0;
	}
}

@keyframes mmslideInBg {
	from {
		opacity : 0;
	}
	to {
		opacity : .7;
	}
}

@keyframes mmslideOutBg {
	from {
		opacity : .7;
	}
	to {
		opacity : 0;
	}
}

.modal {
	display : none;
}

.modal.is-open {
	display : block;
}

.modal[aria-hidden="false"] .modal__overlay {
	animation : mmslideIn .3s linear;
}

.modal[aria-hidden="true"] .modal__overlay {
	animation : mmslideOut .3s linear;
	@media screen and (min-width : $break-table) {

	}
}

.modal__overlay {
	position                   : fixed;
	z-index                    : 8888;
	top                        : 0;
	right                      : 0;
	bottom                     : 0;
	left                       : 0;
	display                    : flex;
	overflow-y                 : auto;
	align-items                : normal;
	flex-flow                  : row nowrap;
	justify-content            : normal;
	box-sizing                 : border-box;
	padding                    : 8px;
	background                 : rgba(#242B33, .7);
	-webkit-overflow-scrolling : touch;
	overscroll-behavior        : contain;

	_:-ms-fullscreen, :root & {
		display : block;
	}

	@media screen and (min-width : $break-table) {
		padding : 20px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&--news {
		@media screen and (max-width : 680px) {
			padding : 0;
		}
	}

	.overflowscrolling & {
		overflow-y : scroll;
	}
}

.modal__container {
	position        : relative;
	z-index         : 20;
	box-sizing      : border-box;
	width           : 750px;
	max-width       : 100%;
	margin          : auto;
	border-radius: 7px;
	background      : #FFF;
	background-size : cover;
	padding : 55px 25px 55px 25px;
	@media screen and (min-width : $break-table) {


	}
	@media screen and (min-width : $break-desktop) {
		padding : 55px 75px 55px 75px;
	}
}

.modal__container--system {
	@media screen and (min-width : $break-table) {


	}
	@media screen and (min-width : $break-desktop) {
		padding : 105px 25px 105px 25px;
	}
}

.modal__content {

}

.modal__close {
	font-size                   : 18px;
	position                    : absolute;
	z-index                     : 300;
	top                         : 0;
	right                       : 0;
	box-sizing                  : border-box;
	width                       : 62px;
	height                      : 62px;
	padding                     : 0;
	cursor                      : pointer;
	transition                  : opacity .3s ease;
	opacity                     : 1;
	border                      : 0;
	outline                     : 0;
	background                  : transparent;
	-webkit-tap-highlight-color : transparent;

	&:after {
		content       : "";
		position      : absolute;
		z-index       : 1;
		top           : 50%;
		left          : 50%;
		width         : 32px;
		height        : 32px;
		transform     : translate(-50%, -50%);
		border-radius : 50%;
	}

	&:before {
		content    : "";
		position   : absolute;
		z-index    : 10;
		top        : 50%;
		left       : 50%;
		width      : 16px;
		height     : 16px;
		transform  : translate(-50%, -50%);
		background : url("../images/close-min.svg") no-repeat 50% 50%;
	}

	&:hover {
		opacity : .7;
	}
}

.modal__bg {
	position : fixed;
	z-index  : 1;
	top      : 0;
	right    : 0;
	bottom   : 0;
	left     : 0;
	cursor   : pointer;
}

.form-modal__title {
