	font-size: 20px;
	line-height: 100%;
	color: #412b92;
	margin-bottom: 15px;
	text-align: center;
	@media screen and (min-width : $break-table) {


	}
	@media screen and (min-width : $break-desktop) {
		font-size: 28px;
		margin-bottom: 30px;
	}
}
.form-modal__top {
	margin-bottom: 15px;
}

.form-modal__text {
	font-weight: 400;
	font-size: 16px;
	line-height: 100%;
	color: #000;
	margin-bottom: 10px;
	text-align: center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size: 20px;
	}

}

.form-modal__subtext{
	font-weight: 400;
	font-size: 10px;
	line-height: 100%;
	color: #000;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size: 12px;
	}
}

.form--modal {
	.form__wrapper {
		max-width: 200px;
		margin: 0 auto 25px auto;
		display: flex;
		flex-direction: column;
		gap: 10px;
	}

	.b-btn--primary {
		position: relative;
		left: 50%;
		transform: translateX(-50%);
		margin-top: 30px;
	}

	.form__wrap {
		max-width: 180px;
		margin: 0 auto;

		@media screen and (min-width : $break-table) {
			max-width: unset;
			margin: 0;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.privacy-policy {
	font-weight: 400;
	font-size: 10px;
	line-height: 100%;
	color: #000;
	margin-bottom: 15px;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size: 12px;
		line-height: 100%;
	}
	a {
		text-decoration: underline;
		color: #412b92;
	}
}
