:root {
  --body-font-size: 16px;
  --body-font: "Ubuntu", system-ui, -apple-system, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  --body-font-alt: Georgia, Times, Times New Roman, serif;
  --body-line-height: 161%;
  --body-color: #0B0334;
  --body-background: #fff;
}

.ubuntu-regular {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.ubuntu-medium {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.ubuntu-bold {
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  font-style: normal;
}

html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

body {
  margin: 0;
}

section,
aside,
nav,
footer,
main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 700;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

a,
button {
  -webkit-tap-highlight-color: transparent;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
  -webkit-tap-highlight-color: transparent;
  outline: 0;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 0;
}

fieldset {
  padding: 0;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  font-family: var(--body-font);
  font-size: var(--body-font-size);
  line-height: var(--body-line-height);
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: var(--body-color);
  background: var(--body-background);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: 0;
}

svg {
  max-height: 100%;
}

select:not([multiple]) {
  vertical-align: middle;
}

table {
  width: 100%;
  max-width: 100%;
  border-spacing: 0;
  border-collapse: separate;
}

th {
  font-weight: var(--table-header-font-weight);
  text-align: left;
}

th,
td {
  line-height: inherit;
  padding: var(--table-cell-padding);
}

th {
  vertical-align: bottom;
}

td {
  vertical-align: top;
}

a {
  text-decoration: underline;
  color: var(--link-color);
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--link-color-hover);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: var(--heading-font-weight);
  line-height: var(--heading-line-height);
  margin: var(--heading-margin);
}

p {
  margin: var(--paragraph-margin);
}
p:last-child {
  margin-bottom: 0;
}

dl,
ol,
ul {
  margin: var(--list-margin);
}

ol {
  list-style: decimal;
}

h1 {
  font-size: var(--h1);
}

h2 {
  font-size: var(--h2);
}

h3 {
  font-size: var(--h3);
}

h4 {
  font-size: var(--h4);
}

h5 {
  font-size: var(--h5);
}

hr {
  display: block;
  height: 1px;
  margin: 30px 0;
  border: 0;
  background: #DEDEDE;
}

::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.hidden-box {
  display: none;
  visibility: hidden;
}

iframe {
  border: 0;
  outline: 0;
}

.videoWrapper {
  position: relative;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 */
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.text-center {
  text-align: center;
}

header,
nav,
section {
  display: block;
}

@keyframes dropdown-display {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(-0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
@keyframes dropdown-display-up {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
blockquote {
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  line-height: 189%;
  position: relative;
  max-width: 830px;
  margin: 42px 0;
  padding-left: 27px;
  color: #000;
}
@media screen and (min-width: 767px) {
  blockquote {
    margin: 28px 0;
  }
}
@media screen and (min-width: 1439px) {
  blockquote {
    margin: 48px 0;
  }
}
blockquote:after {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 0;
  width: 3px;
  background: #000;
}

.form__input {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.25;
  box-sizing: border-box;
  max-width: 100%;
  height: auto;
  padding: 10px 10px;
  transition: border-color 0.3s ease;
  color: #000;
  border: 1px solid #7a7a7a;
  border-radius: 7px;
  outline: 0;
  background-color: #f5f4f4;
  -webkit-appearance: none;
}
.form__input::placeholder {
  font-weight: 400;
  color: #7a7a7a;
}
.form__input:focus {
  border-color: #412b92;
}
.form__input.error {
  color: var(--red);
  border-color: var(--red);
}
.form__input.disabled, .form__input[disabled] {
  cursor: default;
  color: #939393;
  border-color: #BCBCBC;
  background-color: #F9F9F9;
}
.form__input[readonly] {
  cursor: default;
  color: #939393;
  border-color: #BCBCBC;
  background-color: #F9F9F9;
}

textarea {
  overflow: auto;
  height: auto;
}

.error-message {
  order: 1;
  margin-top: 5px;
  color: var(--red);
}

label.checkbox-style,
.checkbox-style {
  width: 18px;
  height: 18px;
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  cursor: pointer;
  user-select: none;
  outline: 0;
  -webkit-tap-highlight-color: transparent;
  flex-shrink: 0;
}
label.checkbox-style > span,
.checkbox-style > span {
  position: relative;
  display: inline-block;
  padding: 2px 0 2px 28px;
  vertical-align: top;
  color: #A7A8B1;
}
label.checkbox-style > span:before,
.checkbox-style > span:before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  transition: border-color 0.3s ease, background-color 0.3s ease;
  border: 1px solid #412B92;
  border-radius: 3px;
}
label.checkbox-style > span:after,
.checkbox-style > span:after {
  content: "";
  position: absolute;
  z-index: 5;
  top: 3px;
  left: 3px;
  width: 12px;
  height: 13px;
  transition: opacity 0.3s ease;
  opacity: 0;
  background-image: url("../images/icon/check.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
label.checkbox-style input[type=checkbox],
.checkbox-style input[type=checkbox] {
  position: absolute;
  top: 0;
  left: -30px;
  width: 1px;
  height: 1px;
  outline: 0;
  -webkit-tap-highlight-color: transparent;
}
label.checkbox-style input[type=checkbox]:checked ~ span:before,
.checkbox-style input[type=checkbox]:checked ~ span:before {
  background: #412B92;
}
label.checkbox-style input[type=checkbox]:checked ~ span:after,
.checkbox-style input[type=checkbox]:checked ~ span:after {
  opacity: 1;
}
label.checkbox-style input[type=checkbox].disabled ~ span:after, label.checkbox-style input[type=checkbox][disabled] ~ span:after,
.checkbox-style input[type=checkbox].disabled ~ span:after,
.checkbox-style input[type=checkbox][disabled] ~ span:after {
  background: #E3E3E3;
}

.checkbox-wrap {
  display: flex;
  align-items: center;
  gap: 12px;
}
.checkbox-wrap__text {
  font-weight: 400;
  font-size: 10px;
  line-height: 100%;
  color: #000;
}
@media screen and (min-width: 1439px) {
  .checkbox-wrap__text {
    font-size: 12px;
  }
}

.form__row {
  position: relative;
}
.form__row:first-child {
  margin-top: 0;
}
.form__row input[type=text],
.form__row input[type=email],
.form__row input[type=search],
.form__row input[type=number],
.form__row input[type=tel],
.form__row input[type=password],
.form__row select,
.form__row textarea {
  width: 100%;
  vertical-align: top;
}
.form__row > label:not(.checkbox-style) {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2;
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: 0;
  cursor: pointer;
  color: #A7A8B1;
}
.form__row > label:not(.checkbox-style) > span {
  order: 0;
}
.form__row > label:not(.checkbox-style) .error-message {
  margin-top: 0;
  margin-left: 10px;
}
.form__row > label:not(.checkbox-style) .error-message + span {
  color: var(--red);
}

.form__input {
  position: relative;
  z-index: 1;
}

.form__hint {
  font-size: 12px;
  font-weight: 400;
  font-style: italic;
  position: relative;
  z-index: 2;
  margin: 5px 0 0;
  color: #444;
}

.form__error {
  font-size: 13px;
  font-weight: 500;
  position: relative;
  z-index: 3;
  margin-top: 3px;
  color: var(--red);
}

.form-password {
  position: relative;
}
.form-password input[type=text],
.form-password input[type=password] {
  position: relative;
  z-index: 1;
  padding-right: 50px;
}

.form-password__button {
  position: absolute;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  width: 42px;
  padding: 0;
  cursor: pointer;
  transition: opacity 0.3s ease;
  vertical-align: middle;
  opacity: 0.3;
  border: 0;
  outline: 0;
  background: transparent;
  -webkit-tap-highlight-color: transparent;
}
.form-password__button i {
  position: relative;
  z-index: 1;
  vertical-align: middle;
}
.form-password__button:after {
  content: "";
  position: absolute;
  z-index: 10;
  top: 24px;
  left: 9px;
  display: block;
  width: 24px;
  height: 1px;
  transition: opacity 0.3s ease;
  transform: rotate(-30deg);
  border-top: 1px solid #FFF;
  background: #000;
}
@media screen and (min-width: 1439px) {
  .form-password__button:hover {
    opacity: 1;
  }
}
.form-password__button.active {
  opacity: 1;
}
.form-password__button.active:after {
  opacity: 0;
}

.form__input {
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.form__input input:focus ~ .form__name {
  top: 5px;
  transform: scale(0.8);
  color: var(--gray-500);
  font-size: 12px;
}
.form__input .input-icon {
  padding-right: 44px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.active ~ .form__name {
  top: -5px;
  left: 11px;
  background-color: #f5f4f4;
  transform: scale(0.8);
  font-weight: 400;
  font-size: 10px;
  line-height: 100%;
  color: #412b92;
  white-space: nowrap;
  padding: 0 4px;
}

.form__name {
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: #000;
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  left: 16px;
  display: block;
  transition: top 0.3s ease-in-out, transform 0.3s ease-in-out, font-size 0.3s ease-in-out;
  transform-origin: left;
  pointer-events: none;
  color: rgba(0, 0, 0, 0.5);
}

.b-btn {
  line-height: 1.25;
  position: relative;
  display: inline-flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
  box-sizing: border-box;
  padding: 6px 20px;
  user-select: none;
  transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 20px;
  font-weight: 700;
  font-size: 24px;
  color: #fff;
  gap: 10px;
  cursor: pointer;
}
.b-btn:hover, .b-btn:focus-within, .b-btn.focus {
  outline: 0;
}
.b-btn.disabled, .b-btn:disabled {
  cursor: default;
  opacity: 0.45;
  background: inherit;
  box-shadow: none;
}
.b-btn > svg {
  display: block;
  flex: 0 0 auto;
}
.b-btn--big {
  font-size: 18px;
  min-height: 59px;
  padding: 4px 30px;
}
.b-btn--small {
  font-size: 15px;
  min-height: 39px;
  padding: 4px 16px;
}
.b-btn--primary {
  background-color: #412b92;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: #fff;
  padding: 11px 15px;
  border-radius: 5px;
}
@media screen and (min-width: 1439px) {
  .b-btn--primary {
    font-size: 24px;
    line-height: 100%;
    letter-spacing: -0.01em;
  }
}
.b-btn--primary svg {
  margin: 0;
  height: 21px;
}
.b-btn--primary:hover, .b-btn--primary:focus-within, .b-btn--primary:active, .b-btn--primary.active {
  background: #5a3d8d;
}
.b-btn--loading:before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  margin-left: -15px;
  transition: border 0.3s ease;
  animation: loadingSpin 1s infinite linear;
  vertical-align: middle;
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
  border-radius: 50%;
}
.b-btn--loading.outline:before {
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-top-color: #000;
}
.b-btn--loading.outline:hover:before {
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
}
.b-btn--full-size {
  display: block;
  width: 100%;
}
.b-btn--full-size + .full-size {
  margin-right: 5px;
}
.b-btn + .b-btn {
  margin-left: 5px;
}

.b-btn-grouping {
  display: inline-flex;
}
.b-btn-grouping .b-btn {
  margin-right: 0;
  border-radius: 0;
}
.b-btn-grouping .b-btn:first-child {
  border-radius: 6pc 0 0 6pc;
}
.b-btn-grouping .b-btn:last-child {
  border-radius: 0 6pc 6pc 0;
}
.b-btn-grouping .b-btn.outline {
  z-index: 1;
  margin-left: -1px;
}
.b-btn-grouping .b-btn.outline:hover {
  z-index: 2;
}
.b-btn-grouping .b-btn.outline.active {
  z-index: 3;
}
.b-btn-grouping .b-btn.outline:first-child {
  margin-left: 0;
}

@keyframes loadingSpin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
  pointer-events: none;
}

input[type=submit].full-size,
input[type=reset].full-size,
input[type=button].full-size {
  width: 100%;
}

.swiper {
  position: relative;
  z-index: 1;
  display: block;
  overflow: hidden;
  overflow: clip;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  list-style: none;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  transition-timing-function: var(--swiper-wrapper-transition-timing-function, initial);
  transition-property: transform;
}

.swiper-android .swiper-slide,
.swiper-ios .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-horizontal {
  touch-action: pan-y;
}

.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  position: relative;
  display: block;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper {
  transform-style: preserve-3d;
}

.swiper-3d {
  perspective: 1200px;
}
.swiper-3d .swiper-slide,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

:root {
  --swiper-navigation-size: 44px;
  --swiper-navigation-top-offset: 50%;
  --swiper-navigation-sides-offset: 10px;
  --swiper-navigation-color: var(--swiper-theme-color);
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  z-index: 10;
  top: var(--swiper-navigation-top-offset, 50%);
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: calc(var(--swiper-navigation-size) / 44 * 27);
  height: var(--swiper-navigation-size);
  margin-top: calc(0px - var(--swiper-navigation-size) / 2);
  padding: 0;
  cursor: pointer;
  color: var(--swiper-navigation-color, var(--swiper-theme-color));
  border: 0;
  outline: 0;
  background: transparent;
  -webkit-tap-highlight-color: transparent;
}
.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  cursor: auto;
  pointer-events: none;
  opacity: 0.35;
}
.swiper-button-prev.swiper-button-hidden,
.swiper-button-next.swiper-button-hidden {
  cursor: auto;
  pointer-events: none;
  opacity: 0;
}
.swiper-navigation-disabled .swiper-button-prev,
.swiper-navigation-disabled .swiper-button-next {
  display: none !important;
}
.swiper-button-prev svg,
.swiper-button-next svg {
  width: 100%;
  height: 100%;
  transform-origin: center;
  object-fit: contain;
}
.swiper-rtl .swiper-button-prev svg,
.swiper-rtl .swiper-button-next svg {
  transform: rotate(180deg);
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  right: auto;
  left: var(--swiper-navigation-sides-offset, 10px);
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: var(--swiper-navigation-sides-offset, 10px);
  left: auto;
}

.swiper-button-lock {
  display: none;
}

:root {
  --swiper-pagination-color: var(--swiper-theme-color);
  --swiper-pagination-left: auto;
  --swiper-pagination-right: 8px;
  --swiper-pagination-bottom: 8px;
  --swiper-pagination-top: auto;
  --swiper-pagination-fraction-color: inherit;
  --swiper-pagination-progressbar-bg-color: rgba(0, 0, 0, 0.25);
